.container-navbar-pc
{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    width: 60vw;
    border: 4px solid rgb(255, 153, 0);
    margin-top: 2%;
}
.image-container
{
    width: 100%;
    height: 40vh;
    display: flex;
    justify-content: center;
}
.image-container > img 
{
    width: 60vw;
    height: 100%;
}
.link-container
{
    display: flex;
    align-items: center;
    flex-direction: row;
    text-align: center;
    justify-content: center;
    height: 6vh;
    color: white;
}
.breed
{
    margin-left: 20px;
}
.logo
{
    height: 5vh;
    width: 3vw;
    color: white;
}

@media only screen and (max-width: 800px)  {
    .container-navbar-pc
    {
        display: none;
        border:none;
    }
}